import React, { Component } from "react";
// import ReactDOM from "react-dom";
import PersonCard from "./Person";
// import Results from "./Results";
import axios from "axios";
const style = {
  marginTop: "0.5%",
  marginBottom: "0.5%",
};
class SearchBar extends Component {
  state = {
    search: "",
    results: [],
    searchRes: [],
  };
  handleInputChange = (e) => {
    const { name, value } = e.target;
    // console.log(name, value);
    this.setState({ [name]: value });
  };

  componentDidMount() {
    axios.get("https://randomuser.me/api/?results=75&nat=us").then((res) => {
      this.setState({ searchRes: res.data.results, results: res.data.results });
      //   console.log(this.state.results.name.first);
    });
  }
  //TODO: doesnt appear to work properly
  handleSubmit = (e) => {
    e.preventDefault();
    // console.log(this.state.results);
    let searchVal = this.state.search;
    const res = [];
    for (let i = 0; i < this.state.results.length; i++) {
      const testee = this.state.results[i].name.first;

      if (testee === searchVal) {
        // console.log(this.state.results[i]);
        res.push(this.state.results[i]);
        //too slow for render
        // this.setState({ searchRes: res });
        //   ReactDOM.render(
        //     <Results searchResults={res} />,
        //     document.getElementById("root")
        //   );

        // }
      }
    }
    this.setState({ searchRes: res });
  };
  handleSort = (e) => {
    const ppl = this.state.results;
    const arr = [];
    for (let i = 0; i < ppl.length; i++) {
      arr.push(ppl[i]);
    }
    // console.log(this.props);
    arr.sort((a, b) => {
      if (a.name.first < b.name.first) {
        return -1;
      }
      if (a.name.first > b.name.first) {
        return 1;
      }
      return 0;
    });
    console.log(arr);
    this.setState({ searchRes: arr });
  };

  resetPage() {
    this.setState({ searchRes: this.state.results });
  }

  render() {
    return (
      <>
        <form
          style={style}
          className="text-center"
          onSubmit={this.handleSubmit}
        >
          <input
            onChange={this.handleInputChange}
            type="text"
            value={this.state.search}
            placeholder="search"
            name="search"
            id="search"
          />
        </form>
        {/* {this.state.results.map((i) => (
          <PersonCard
            firstName={i.name.first}
            lastName={i.name.last}
            phone={i.cell}
            email={i.email}
          />
        ))} */}
        <PersonCard
          person={this.state.searchRes}
          clickEvent={this.handleSort}
        />
      </>
    );
  }
}

export default SearchBar;
